import {
  Card,
  CardActions,
  CardContent,
  Divider,
  Grid,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import DevicesIcon from "@mui/icons-material/Devices";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import WarningAmberIcon from "@mui/icons-material/WarningAmber";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";
import Snackbar from "@mui/material/Snackbar";
import IconButton from "@mui/material/IconButton";
import FileCopyIcon from "@mui/icons-material/FileCopy";
import AdfScannerIcon from "@mui/icons-material/AdfScanner";
import Logs from "../Logs/Logs";
import MachinesMenu from "../Menu/MachinesMenu";
import { Machine } from "../../Interfaces/Interface";
import RadioSelect from "../Reports/RadioSelect";
import HeaderBar from "../Header/HeaderBar";
import { Route, Routes } from "react-router-dom";
import { paths } from "../../routes/paths";
import { useNavigate } from "react-router-dom";
import moment from "moment";
import SnackBar from "../SnackBar/SnackBar";

interface MachinesProps {
  MachinesInfo: Machine[];
}

const Machines: React.FC<MachinesProps> = ({ MachinesInfo }) => {
  const data = MachinesInfo;
  const [isSnackbarOpen, setSnackbarOpen] = useState(false);
  const [copiedItemId, setCopiedItemId] = useState<string>("");
  const [ViewItemId, setViewItemId] = useState<string | null>(null);

  const [onStatusData, setOnStatusData] = useState<Machine[]>([]);
  const [offStatusData, setOffStatusData] = useState<Machine[]>([]);

  // console.log(data)

  useEffect(() => {
    const currentMoment = moment(); // Get the current date-time once

    const newOnStatusData: Machine[] = [];
    const newOffStatusData: Machine[] = [];

    data.forEach((item: Machine) => {
      // Parse the API date-time string using Moment.js
      const apiMoment = moment(item.lastSyncDateTime);

      // Check if the date, month, year, and hour are the same
      // const isSameDateTime = currentMoment.isSame(apiMoment);

      // Calculate the difference in minutes
      const differenceInMinutes = currentMoment.diff(apiMoment, "minutes");

      // Check if the difference is less than 10 minutes and the date, month, year, and hour are the same
      if (Math.abs(differenceInMinutes) < 10) {
        newOnStatusData.push(item);
      } else {
        newOffStatusData.push(item);
      }
    });

    // Update the state variables with the new data
    setOnStatusData(newOnStatusData);
    setOffStatusData(newOffStatusData);

    return () => {};
  }, [data]);

  useEffect(() => {
    if (isSnackbarOpen) {
      setTimeout(() => {
        setSnackbarOpen(false);
      }, 2000);
    }
    return () => {};
  }, [isSnackbarOpen]);

  const Navigate = useNavigate();

  const handleCardClick = (guid: string) => {
    Navigate(paths.ATTENDANCEREPORTS, { state: { Guid: guid } });
  };
  const color = (item: (typeof data)[0]) => {
    if (item.temperature >= 55 || item.humidity >= 85) {
      return "border-2 border-red-500 scale-105 mx-1";
    } else if (
      (item.temperature >= 45 && item.temperature < 55) ||
      (item.humidity >= 75 && item.humidity < 85)
    ) {
      return "border-2 border-amber-500 scale-105 mx-1";
    } else {
      return " ";
    }
  };
  let textToCopy = "";
  const handleCopyClick = (guid: string) => {
    const entryWithId = data.find((item) => item.guid === guid);
    if (entryWithId) {
      const macForId = entryWithId.mac;
      // textToCopy = entryWithId.mac;
      setCopiedItemId(macForId);
      navigator.clipboard.writeText(entryWithId.mac);
      setSnackbarOpen(true);
    } else {
      console.log(`No entry found with id ${entryWithId}`);
    }
  };

  const handleviewlogs = (guid: string) => {
    setViewItemId(guid);
  };
  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };
  const renderMachineCard = (SectionData: typeof data, status: boolean) => (
    <Grid container spacing={2} sx={{ paddingX: 4, paddingY: 2 }}>
      {SectionData.map((item, index) => (
        <Grid
          item
          key={item.guid}
          xs={12}
          sm={6}
          md={4}
          lg={3}
          sx={{}}
          className=""
        >
          <Card className={color(item)}>
            <CardContent sx={{}} className="">
              <Grid
                container
                spacing={2}
                //   alignItems="center"
                className="flex flex-row "
              >
                <Grid
                  md={12}
                  sx={{ paddingLeft: 2, paddingTop: 2 }}
                  className=" flex flex-row "
                >
                  <Grid
                    item
                    sx={{ paddingBottom: 2, marginLeft: 2 }}
                    className=""
                    onClick={() => handleCardClick(item.guid)}
                  >
                    {/* <AdfScannerIcon sx={{ height: 40, width: 40 }} /> */}
                    <DevicesIcon sx={{ height: 40, width: 40 }} />
                  </Grid>
                  <Grid item md={10} sx={{ marginLeft: 4 }} className="">
                    <Grid
                      container
                      md={12}
                      className="flex flex-row "
                      onClick={() => handleCardClick(item.guid)}
                    >
                      <Typography variant="body1" component="div">
                        Machine {index + 1} &nbsp;
                      </Typography>
                      <Typography
                        variant="body1"
                        color="text.secondary"
                        component="div"
                      >
                        {item.location}
                      </Typography>
                    </Grid>
                    <Grid container className="" alignItems="center">
                      <Typography
                        variant="caption"
                        color="text.secondary"
                        component="div"
                        sx={{ fontStyle: "italic" }}
                      >
                        Mac: {item.mac}
                      </Typography>

                      <Typography>
                        <IconButton onClick={() => handleCopyClick(item.guid)}>
                          <ContentCopyIcon
                            color="primary"
                            sx={{ height: 15, width: 15, marginTop: "-4px" }}
                          />
                        </IconButton>
                      </Typography>

                      {/* <Typography variant="caption" className="" sx={{textAlign:"right"}}>
                        <DeviceThermostatIcon fontSize="small"/>
                        {item.temp}
                      </Typography> */}
                    </Grid>
                    <Grid
                      container
                      md={9}
                      onClick={() => handleCardClick(item.guid)}
                    >
                      <Typography
                        variant="caption"
                        color="text.secondary"
                        component="div"
                        sx={{ marginTop: "-8px" }}
                        onClick={() => handleCardClick(item.guid)}
                      >
                        {item.ip}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Typography className="">
                    <MachinesMenu />
                  </Typography>
                </Grid>
                <Grid
                  md={12}
                  sx={{ paddingLeft: 2, marginBottom: -2 }}
                  className=" flex flex-row "
                >
                  <Grid
                    item
                    sx={{ marginLeft: 1.5, marginTop: 3 }}
                    className=""
                    onClick={() => handleCardClick(item.guid)}
                  >
                    <Typography variant="body2">
                      {status ? (
                        <>
                          <FiberManualRecordIcon style={{ color: "#4bd16f" }} />
                          On
                        </>
                      ) : (
                        <>
                          <WarningAmberIcon style={{ color: "red" }} />
                          Off
                        </>
                      )}
                    </Typography>
                  </Grid>
                  <Grid
                    item
                    sx={{ paddingLeft: 2, marginLeft: 2 }}
                    className=" "
                    onClick={() => handleCardClick(item.guid)}
                  >
                    <Typography
                      variant="caption"
                      color="text.secondary"
                      component="div"
                      sx={{ marginLeft: 0.5 }}
                    >
                      Synced : {item.lastSyncDateTime}
                    </Typography>
                    <Grid container className=" flex flex-row">
                      <Grid className="" sx={{ paddingRight: 1 }}>
                        <Typography
                          variant="caption"
                          sx={{
                            color:
                              item.temperature >= 45 && item.temperature < 55
                                ? "#FFBA00"
                                : item.temperature >= 55
                                ? "red"
                                : "",
                            fontStyle: "italic",
                          }}
                        >
                          {/* <DeviceThermostatIcon fontSize="small"/> */}
                          Temp : {item.temperature}Â°C ;
                        </Typography>
                      </Grid>
                      <Grid className="">
                        <Typography
                          variant="caption"
                          sx={{
                            color:
                              item.humidity >= 75 && item.humidity < 85
                                ? "#FFBA00"
                                : item.humidity >= 85
                                ? "red"
                                : "",
                            fontStyle: "italic",
                          }}
                        >
                          {/* <WaterDropIcon fontSize="small" /> */}
                          Humidity : {item.humidity}%
                        </Typography>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </CardContent>

            <CardActions className=" ">
              <Typography
                onClick={() => handleviewlogs(item.guid)}
                sx={{ textAlign: "right" }}
                className=" max-w-xs	"
              >
                <Divider />
                <Logs
                  index={index}
                  subtitle={item.location}
                  ControllerGuid={item.guid}
                />
              </Typography>
            </CardActions>
          </Card>
        </Grid>
      ))}
    </Grid>
  );

  return (
    <Grid container spacing={2} sx={{ padding: 4 }}>
      {onStatusData.length !== 0 ? (
        <>
          <Typography variant="h5" sx={{ padding: 2 }}>
            On Machines
          </Typography>
          {renderMachineCard(onStatusData, true)}
        </>
      ) : null}

      {offStatusData.length !== 0 ? (
        <>
          <Typography variant="h5" sx={{ padding: 2 }}>
            Off Machines
          </Typography>
          {renderMachineCard(offStatusData, false)}
        </>
      ) : null}

      {/* <Snackbar
        open={isSnackbarOpen}
        autoHideDuration={2000}
        onClose={handleSnackbarClose}
        message={`Copied to clipboard! (Mac : ${copiedItemId})`}
        action={
          <IconButton
            size="small"
            color="inherit"
            onClick={handleSnackbarClose}
          >
            <FileCopyIcon fontSize="small" />
          </IconButton>
        }
      /> */}
      <SnackBar
        ope={isSnackbarOpen}
        mes={`Copied to clipboard! (Mac : ${copiedItemId})`}
      />
    </Grid>
  );
};

export default Machines;
